.landing-root {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #2c5364,
    #203a43,
    #0f2027
  );
  background: linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
  text-align: center;
  color: white;
  padding-top: 10%;
}

.landing-text {
  font-size: 1.5rem;
  line-height: 3rem;
  margin: 50px;
}

@media only screen and (max-width: 992px) {
  .landing-text {
    font-size: 1rem;
    line-height: 2rem;
    margin: 50px;
  }
}

.question-root {
  background: #c31432; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #240b36,
    #c31432
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    right,
    #240b36,
    #c31432
  );
  background: linear-gradient(
    to left,
    #240b36,
    #c31432
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
  text-align: center;
  color: white;
  padding-top: 10%;
}

.question-text {
  font-size: 1.5em;
  margin: 50px;
}

.vis-root {
  background: #667db6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #667db6,
    #0082c8,
    #0082c8,
    #667db6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    right,
    #667db6,
    #0082c8,
    #0082c8,
    #667db6
  );
  background: linear-gradient(
    to left,
    #667db6,
    #0082c8,
    #0082c8,
    #667db6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
  text-align: center;
  color: white;
  padding-top: 10%;
}

.vis-text {
  font-size: 1.5em;
  margin: 50px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* blob css */
#counter-label {
  color: #a42828;
}

.blob-header h5, #blob-legend {
display: inline;
}

.blob-header {
margin-bottom: 90px;
width: 60%;
margin-left: auto;
margin-right: auto;
}

.svg-blob-canvas {
width: 100%;
height: 60vh;
margin-left: auto;
margin-right: auto;
display: block;
}

#counter-header {
font-size: 2em;
text-align: center;
}

#counter-label {
font-size: 3em;
width: 111px;
margin: 0;
}

#blob-explanation {
font-size: 1.3em;
width: 100%;
margin: 0 auto;
text-align: center;
}

#blob-explanation>h5 {
opacity: 0;
}

